.content {
	display: grid;
	width: 100%;
	margin: 0 auto;
	padding: 10vh 25px;
	max-width: 1300px;
	grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
	grid-auto-rows: 200px;
	grid-gap: 4rem;
	grid-row-gap: 6rem;
	-webkit-justify-content: center;
	        justify-content: center;
	list-style: none;
    text-decoration: none;
}


.content__item {
	width: 100%;
	height: 100%;
	margin: 0;
	padding: 0;
	counter-increment: itemcounter;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: center;
	        justify-content: center;
	position: relative;
	z-index: 0;
	pointer-events: none;
}

.content__item::before {
	color: var(--color-number);
	position: absolute;
	top: 0;
	left: 0;
}

.button {
	pointer-events: auto;
	cursor: pointer;
	background: #BFB7A7;
	border: none;
	padding: 1.5rem 3rem;
	margin: 0;
	font-family: inherit;
	font-size: inherit;
	position: relative;
	display: inline-block;
}

.button::before,
.button::after {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
}

.button--calypso {
	overflow: hidden;
	font-family: freight-display-pro, serif;
	font-size: 1.15rem;
	border-radius: 0.85rem;
	color: #fff;
}

.button--calypso a {
	display: block;
	position: relative;
	mix-blend-mode: difference;
	z-index: 10;
    text-decoration: none;
}

.button--calypso:hover a {
	-webkit-animation: MoveScaleUpInitial 0.3s forwards, MoveScaleUpEnd 0.3s forwards 0.3s;
	        animation: MoveScaleUpInitial 0.3s forwards, MoveScaleUpEnd 0.3s forwards 0.3s;
    color: white;
}

@-webkit-keyframes MoveScaleUpInitial {
	to {
		-webkit-transform: translate3d(0,-105%,0) scale3d(1,2,1);
		        transform: translate3d(0,-105%,0) scale3d(1,2,1);
		opacity: 0;
	}
}

@keyframes MoveScaleUpInitial {
	to {
		-webkit-transform: translate3d(0,-105%,0) scale3d(1,2,1);
		        transform: translate3d(0,-105%,0) scale3d(1,2,1);
		opacity: 0;
	}
}

@-webkit-keyframes MoveScaleUpEnd {
	from {
		-webkit-transform: translate3d(0,100%,0) scale3d(1,2,1);
		        transform: translate3d(0,100%,0) scale3d(1,2,1);
		opacity: 0;
        
	}
	to {
		-webkit-transform: translate3d(0,0,0);
		        transform: translate3d(0,0,0);
		opacity: 1;
	}
}

@keyframes MoveScaleUpEnd {
	from {
		-webkit-transform: translate3d(0,100%,0) scale3d(1,2,1);
		        transform: translate3d(0,100%,0) scale3d(1,2,1);
		opacity: 0;
        
	}
	to {
		-webkit-transform: translate3d(0,0,0);
		        transform: translate3d(0,0,0);
		opacity: 1;
	}
}

.button--calypso::before {
	content: '';
	background: #000;
	width: 120%;
	height: 0;
	padding-bottom: 120%;
	top: -110%;
	left: -10%;
	border-radius: 50%;
	-webkit-transform: translate3d(0,68%,0) scale3d(0,0,0);
	        transform: translate3d(0,68%,0) scale3d(0,0,0);
}

.button--calypso:hover::before {
	-webkit-transform: translate3d(0,0,0) scale3d(1,1,1);
	        transform: translate3d(0,0,0) scale3d(1,1,1);
	transition: -webkit-transform 0.4s cubic-bezier(0.1, 0, 0.3, 1);
	transition: transform 0.4s cubic-bezier(0.1, 0, 0.3, 1);
	transition: transform 0.4s cubic-bezier(0.1, 0, 0.3, 1), -webkit-transform 0.4s cubic-bezier(0.1, 0, 0.3, 1);
    
}

.button--calypso::after {
	content: '';
	background: #000;
	-webkit-transform: translate3d(0,-100%,0);
	        transform: translate3d(0,-100%,0);
	transition: -webkit-transform 0.4s cubic-bezier(0.1, 0, 0.3, 1);
	transition: transform 0.4s cubic-bezier(0.1, 0, 0.3, 1);
	transition: transform 0.4s cubic-bezier(0.1, 0, 0.3, 1), -webkit-transform 0.4s cubic-bezier(0.1, 0, 0.3, 1);
}

.button--calypso:hover::after {
	-webkit-transform: translate3d(0,0,0);
	        transform: translate3d(0,0,0);
	transition-duration: 0.05s;
	transition-delay: 0.4s;
	transition-timing-function: linear;
}

